/* RESETS */
*,
*::before,
*::after {
  box-sizing: border-box;
}
*:focus {
  outline-style:auto;
  outline-width: 1px;
  outline-color: #5755b3;
  outline-offset: 0;
}
html {
  font: 62.5% / 1.15 sans-serif;
}
h1,
h2 {
  margin-bottom: 0;
}
ul {
  list-style: none;
  padding: 0;
}

button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
}
button::-moz-focus-inner {
  border: 0;
}
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
button,
input {
  overflow: visible;
  border-radius: 0.5rem;
  
}
input[type="text"] {
  border-radius: 0.5rem;
  width: 40rem;
  padding-inline-start: 30px;
  
  
}
input[type="password"] {
  border-radius: 0.5rem;
  width: 40rem;
  padding-inline-start: 30px;
  
}
body {
  width: 100%;
  
  margin: 0 auto;
  font: 1.6rem/1.25 Arial, sans-serif;
  background-color: #f5f5f5;
  color: #4d4d4d;
}
@media screen and (min-width: 620px) {
  body {
    font-size: 1.9rem;
    line-height: 1.31579;
  }
}
.system{
  display: block;
  padding-left: 25rem;
  padding-right: 25rem;
}
.sistema{
  display:flexbox;
  justify-content: center;
  
}
/*END RESETS*/
/* GLOBAL STYLES */
.caixa {
  border-width:0.5px;
  border-style:solid;
  border-color: #d3d3d3;
  background-color: #ffffff;
  font-size: 1.6rem;
  
  /*-webkit-border-radius: 20px 0 20px 20px;
  border-radius: 20px 0 20px 20px;
  padding: 0.1% 0.1% 0.1% 0.1%;
  padding-top: 5px;*/
  padding-left: 2rem;
  padding-block: 1rem;
  /*margin-top: 5px;*/
  /*background-image: -webkit-linear-gradient(top, #d2d4da, #200B66);
  background-image:         linear-gradient(to bottom, #d2d4da, #200B66);*/
  /*opacity: 0.8;*/
}
.texto{
  text-decoration: none;
  color: rgb(0, 0, 0);
}
.textologin{
  height: 2.7rem;
  font-size: 1.45rem;
  font-weight:bolder;
  color: rgb(48, 6, 6);
  
}
.form-group > input[type="text"] {
  display: inline-block;
  margin-top: 0.4rem;
}
.btn {
  padding: 1rem 1rem 1rem;
  border: 0.2rem solid #4d4d4d;
  cursor: pointer;
  text-transform: capitalize;
}
.btn.toggle-btn {
  border-width: 1px;
  border-color: #d3d3d3;
}
.btn.toggle-btn[aria-pressed="true"] {
  text-decoration: underline;
  border-color: #4d4d4d;
}
.btn__danger {
  color: #fff;
  background-color: #ca3c3c;
  border-color: #bd2130;
}
.btn__filter {
  border-color: lightgrey;
}
.btn__primary {
  color: #fff;
  background-color: rgb(113, 115, 228);
}
.botaoentrar {
  text-decoration: none;
  border-radius: 0.5rem;
  margin-left: 39%;
}
.btn-group {
  display: flex;
  justify-content: space-between;
}
.btn-group > * {
  flex: 1 1 49%;
}
.btn-group > * + * {
  margin-left: 0.8rem;
}
[class*="stack"] > * {
  margin-top: 0;
  margin-bottom: 0;
  align-content: center;
  /*margin-left: 1rem;*/
}
.stack-small > * + * {
  margin-top: 1.25rem;
}
.stack-large > * + * {
  margin-top: 2.5rem;
}
@media screen and (min-width: 550px) {
  .stack-small > * + * {
    margin-top: 1.4rem;
  }
  .stack-large > * + * {
    margin-top: 2.8rem;
  }
}
.stack-exception {
  margin-top: 1.2rem;
}
/* END GLOBAL STYLES */
.divgrande{
  background-image: url('./giphy.gif');
  background-position:initial;
  background-size: 100vw;
  height: 100%;
}
.divisao{
  display:flex;
  flex-direction: row;
  justify-content: center;
  
  padding-bottom: 17rem;
}
.todoapp {
  border-radius: 0.7rem;
  position: center;
  align-self: center;
  max-width: 44rem;
  background: #ffffff;
  margin: 8rem 2rem 0 1rem;
  padding: 0.5rem;
  position: relative;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 2.5rem 5rem 0 rgba(0, 0, 0, 0.1);
}
@media screen and (min-width: 550px) {
  .todoapp {
    padding: 2rem;
  }
}
.todoapp > * {
  max-width: 0rem;
  margin-left: auto;
  margin-right: auto;
}
.todoapp > form {
  max-width: 100%;
}
.todoapp > h4 {
  display: block;
  max-width: 100%;
  text-align: center;
  padding: 0;
  margin: 0;
  margin-bottom: 0;
}
.label__lg {
  line-height: 1.01567;
  font-weight: 300;
  padding: 0.8rem;
  margin-bottom: 1rem;
  text-align: center;
}
.input__lg {
  margin-left: 0;
  padding: 1rem;
  border: 1px solid rgb(211, 211, 211);
  /*outline-color: #4a67b896;
  outline-width: 10px;*/
}
.input__lg:focus {
  border-color: #4d4d4d;
  box-shadow: inset 0 0 0 2px;
}
[class*="__lg"] {
  display: inline-block;
  width: 100%;
  font-size: 1.9rem;
}
[class*="__lg"]:not(:last-child) {
  margin-bottom: 1rem;
}
@media screen and (min-width: 620px) {
  [class*="__lg"] {
    font-size: 2.4rem;
  }
}